<template>
  <div class="home">
    <img id="logo_img" width="100" alt="Pilot Logo" src="../assets/Pliot.png">
    <section>
      <PageTitle ref="page_title" :titleTxt="titleText" :instruction="{comment: 'My First jsGame!',date:'28/11/2023'}"/>
      <ChildComponent ref="child_compo"/>
      <button type="button" @click="changeChildData">게임 시작 : {{ clickGameBtn }} 회</button>
    </section>
    <section>
      <PuzzleGame/>
    </section>
  </div>
</template>

<script>
import PageTitle from "../components/PageTitle.vue";
import ChildComponent from "@/components/ChildComponent.vue";
import PuzzleGame from "@/components/PuzzleGame.vue";
export default {
  name: 'HomeView',
  components: {
    PageTitle,
    ChildComponent,
    PuzzleGame,
  },
  data(){
    return{
      titleText : "This is Shooting Game",
      clickCount : 0,
    }
  },
  mounted(){
    this.$refs.child_compo.$refs.btn.click();
    this.$refs.child_compo.callFromParent();
  },
  computed:{
    clickGameBtn(){
      return this.$store.state.count+1
    }
  },
  methods:{
    changeChildData(){
      this.$refs.page_title.titleTxt = "Actually, This is Puzzle Game!";
      this.$store.commit('increment');
    }
  }
}
</script>

<style scoped>
  #logo_img{
    display: block;
    margin: auto;
    padding-top: 30px;
  }
</style>