import {createStore} from "vuex";

const store = createStore({
  state() {
    // 여기에 상태를 정의합니다.
    return{
      count:0,
    }
  },
  mutations: {
    // 여기에 상태를 변화시키는 메서드를 정의합니다.
    increment(state){
      state.count++;
    }
  },
  actions: {
    // 여기에 비동기 로직을 수행하거나 여러 뮤테이션을 연결하는 메서드를 정의합니다.
  },
  getters: {
    // 여기에 state를 기반으로 계산된 값을 반환하는 메서드를 정의합니다.
  }
})

export default store;