<template>
  <div>
    <div id="screenBox" className="box">
			<h1>게임 종료 화면</h1>
    </div>
    <div id="buttonBox" className="box">
			<!--버튼을 클릭하면 play-again이벤트가 호출-->
      <button @click="gameTitle">처음으로</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "GameOver",
  methods: {
    gameTitle(){
        this.$emit('play-again');
      }
    }
}
</script>
<style scoped>
  div#screenBox{
    height: 250px;
    background-color: blanchedalmond;
  }
  div#screenBox>h1{
    line-height: 250px;
  }
</style>