<template>
  <div id="container">
    <button type="button" @click="childFunc" ref="btn">Click</button>
    <p>Click : {{ comment }}</p>

    <section>
      <h1>{{ msg }}</h1>
    </section>
  </div>
</template>
<script>
export default {
  data(){
    return{
      comment : "",
      msg:"",
    }
  },
  methods:{
    childFunc(){
      this.comment = "Click";
    },
    callFromParent(){
      this.comment = "From Parent";
    }
  }
}
</script>
