<template>
  <div id="container">
		<!--초기 화면-->
    <div v-if="status === 'beginning'">
      <!--start-game이벤트가 발생되면 startGame메서드가 호출-->
      <GameBeginning @start-game="startGame" />
    </div>

		<!--게임 화면-->
    <div v-if="status === 'gaming'">
      <!-- stop-game이벤트가 발생되면 stopGame메서드 호출-->
      <GameStart @stop-game="stopGame"/>
    </div>

		<!--게임종료 화면-->
    <div v-if="status === 'stopping'">
      <!--play-again이벤트가 발생되면 playAgain메서드를 호출하여 게임 다시 시작-->
      <GameOver @play-again="playAgain" />
    </div>
  </div>
</template>

<script>
  //component 불러오기
  import GameBeginning from "./GameBeginning.vue";
  import GameStart from "./GameStart.vue";
  import GameOver from "./GameOver.vue";

  export default {
    name: "PuzzleGame",
    components: { GameBeginning, GameStart, GameOver },

		data() {  //데이터
      return {
        status: "beginning", //현재 페이지 상태
      };
    },
    methods: {
      startGame() {
        this.status = "gaming";//status를 gaming으로 변경
      },

      stopGame() {
        this.status = "stopping";//status를 stopping으로 변경
      },

      playAgain() {
        this.status = "beginning";//status를 beginning으로 변경
      }
    }
  }
</script>

<style scoped>
  div#container{
    width: 80%; height: 400px;
    background-color: azure;
    margin: 30px auto;
    padding-top: 30px;
    box-sizing: border-box;
  }
  div#container>div{
    width:50%; height:300px;
    background-color: blanchedalmond;
    box-sizing: border-box;
    margin: auto;
  }
</style>