<template>
  <div>
    <div id="screenBox" className="box">
			<h1>게임 화면</h1>
    </div>
    <div id="buttonBox" className="box">
			<!--버튼을 클릭하면 stop-game이벤트가 호출-->
      <button @click="gameOver">게임오버</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "GameStart",
  methods: {
    gameOver(){
        this.$emit('stop-game');
      }
    }
}
</script>
<style scoped>
  div#screenBox{
    height: 250px;
    background-color: blanchedalmond;
  }
  div#screenBox>h1{
    line-height: 250px;
  }
</style>