<template>
  <div class="container">
    <h2>DataBinding 컴포넌트입니다.</h2>
    <div>
      <h1>컴포넌트 등록</h1>
      <p>구성요소를 사용하면 웹 페이지를 작업하기 쉬운 작은 분류로 나눔</p>
      <p>자체 콘텐츠 논리를 사용하여 vue구성요소 시작 가능</p>
    </div>
    <hr />
    <div>
      <h1>데이터 바인딩</h1>
      <ul>
        <li>vue =&gt; 양방향 데이터바인딩 지원</li>
        <li>
          양방향 데이터바인딩 =&gt; live server 처럼 한 쪽이 바뀌면 다른 쪽도
          갱신되는 동기화 상태
        </li>
      </ul>
      <h3>메세지 출력 : {{ message }}</h3>
      <p>vue에서 제공하는 특수 속성이므로 v- 를 붙임.</p>
      <h3>예시 코드</h3>
      <p>{{ favor }}</p>
      <h3>html 출력은 v-html 디렉티브 사용</h3>
      <p>vue에서 제공하는 특수 속성이므로 v- 를 붙임.</p>
      <p><span style="color: plum" v-html="htmlString"></span></p>
    </div>
    <hr />
  </div>
</template>

<script>
export default {
  data() {
    return {
      messgae: "inSANe",
      htmlString: `<span style="color:skyblue">span 태그 안에 span태그를 넣었기 때문에 skyblue색이 됩니다.`,
    };
  },
};
</script>

