<template>
  <div style="text-align: center;">
    <div id="screenBox" className="box">
			<h1>초기화면</h1>
    </div>
    <div id="buttonBox" className="box">
			<!--버튼을 클릭하면 start-game이벤트가 호출-->
      <button @click="gameStart">게임시작</button>
    </div>
  </div>
</template>

<script>
  export default{
    name: "GameBeginning",

    methods: {
      gameStart(){
        this.$emit('start-game');
      }
    }
  };
</script>
<style scoped>
  div#screenBox{
    height: 250px;
    background-color: blanchedalmond;
  }
  div#screenBox>h1{
    line-height: 250px;
  }
</style>